<template>
  <div id="review-config">
    <h1 class="list-title">
      <span class="tit-text">{{ title }}</span>
    </h1>
    <div class="data-area">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="clipboard"></Icon>
          </h3>
          <div class="btn-box"></div>
        </div>
        <Table border :columns="UserCol" :data="UserData" :loading="loading"></Table>
      </Card>
    </div>
  </div>
</template>

<script>
import { getLocal } from "@/util/util";

export default {
  name: "ReviewConfig",
  data() {
    return {
      title: "",
      apiUrl: "/backend/config-vc/verify-list",
      auth_id: "",
      loading: true,
      TextArr: {
        status: ["", ""]
      },
      //
      ModeModal: {
        modal: false,
        isEdit: false,
        id: "",
        data: {
          level: "",
          between_start: 0,
          between_end: 100,
          remark: ""
        }
      },
      UserCol: [
        {
          title: "",
          width: "70",
          align: "center",
          key: "uid"
        },
        {
          title: "",
          align: "center",
          key: "admin_name"
        },
        {
          title: "",
          align: "center",
          key: "is_status"
        },
        {
          title: "",
          align: "center",
          key: "assign_type"
        },
        {
          title: "",
          key: "operation",
          align: "center",
          width: "330",
          render: (h, params) => {
            //
            return h("div", this.$renderBtn(h, params, this.BtnData));
          }
        }
      ],
      UserData: [], //
      BtnData: []
    };
  },
  created() {
    this.auth_id = getLocal("auth_id");
    this.InitData(this.apiUrl);
  },
  methods: {
    //data
    RemoveObserve(rowdata) {
      return JSON.parse(JSON.stringify(rowdata));
    },
    //
    InitData(url, params = {}) {
      this.loading = true;
      //
      this.$fetch("/backend/Menuauth/listAuthGet", {
        auth_id: this.auth_id
      }).then(d => {
        this.BtnData = d.data.operation;
      });
      //
      return new Promise(resolve => {
        this.$fetch(url, params).then(d => {
          let res = d.data;
          if (res.verify_list.length > 0) {
            res.verify_list.forEach(val => {
              val.assign_type = "";
            });
          }
          this.UserData = res.verify_list;
          this.loading = false;
          resolve();
        });
      });
    },
    //
    UploadData(url, info) {
      return new Promise(resolve => {
        this.$post(url, info)
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve(d.data);
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    QuitOpt(row) {
      this.$Modal.confirm({
        title: "",
        content: "<p></p>",
        onOk: () => {
          this.UploadData("/backend/config-vc/equal-verify", {
            admin_id: row.uid
          }).then(() => {
            this.InitData(this.apiUrl);
          });
        },
        okText: "",
        cancelText: ""
      });
    }
  }
};
</script>


<style lang="less" scoped>
.list-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: normal;
  padding-bottom: 10px;
}
.data-area {
  padding-top: 20px;
  .ivu-table-wrapper {
    border: none;
  }
  .page-box {
    padding: 20px;
    display: flex;
    flex-direction: row-reverse;
  }
}
.card-tit {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.col-center {
  text-align: center;
}
.auto-height {
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
}
.info-width {
  width: 200px;
}
</style>



