<template>
  <div id="listRole">
    <h1 class="list-title">{{ title }}</h1>
    <div class="operate-box">
      <Button type="primary" icon="plus" size="large" @click="AddShow"></Button>
      <Button type="warning" icon="ios-reload" size="large" @click="RefreshList"></Button>
    </div>
    <div class="data-area">
      <Table border :columns="RoleCol" size="large" :data="RoleData" :loading="loading"></Table>
    </div>
    <Modal v-model="AddModal" title="">
      <Form
        ref="AddInfo"
        :model="AddInfo"
        :rules="AddRules"
        label-position="right"
        :label-width="80"
      >
        <FormItem label="">
          <Input v-model="AddInfo.pid" disabled></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model="AddInfo.name"></Input>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="AddInfo.status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="AddInfo.sort"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="AddCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
    <Modal v-model="EditModal" title="">
      <Form
        ref="EditInfo"
        :model="EditInfo"
        :rules="AddRules"
        label-position="right"
        :label-width="100"
      >
        <FormItem label="">
          <Input v-model="EditInfo.id" disabled></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model="EditInfo.name"></Input>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="EditInfo.status">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="EditInfo.sort"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="EditCancel" size="large"></Button>
        <Button type="primary" @click="EditOver" size="large"></Button>
      </div>
    </Modal>
    <Modal v-model="AssignModal" title="" :width="800" @on-ok="AssignOver">
      <Form class="auto-area" label-position="right" :label-width="60">
        <FormItem label="">
          <Input v-model="AssignInfo.role_id" disabled style="width: 350px"></Input>
        </FormItem>
        <FormItem label="">
          <CheckboxGroup v-model="AssignInfo.auth_id">
            <div v-for="(item,index) in AuthList" :key="item.id">
              <Checkbox :label="item.id">{{item.title}}</Checkbox>
              <div class="auth-seclist" v-for="(secItem,secIndex) in item.auth" :key="secItem.id">
                <Checkbox :label="secItem.id">{{secItem.title}}</Checkbox>
                <div
                  class="inline-block"
                  v-for="(thirdItem, thirdIndex) in secItem.auth"
                  :key="thirdItem.id"
                >
                  <p>
                    <Checkbox :label="thirdItem.id">{{thirdItem.title}}</Checkbox>
                  </p>
                  <p v-for="fourItem in thirdItem.auth" :key="fourItem.id">
                    <Checkbox :label="fourItem.id">{{fourItem.title}}</Checkbox>
                  </p>
                </div>
              </div>
            </div>
          </CheckboxGroup>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "listRole",
  data() {
    return {
      title: "",
      loading: true,
      RoleCol: [
        // {
        //   title: '',
        //   key: 'id'
        // },
        {
          title: "",
          align: "center",
          key: "role_name"
        },
        {
          title: "",
          align: "center",
          key: "status"
        },
        {
          title: "",
          align: "center",
          key: "sort"
        },
        {
          title: "",
          width: "190",
          align: "center",
          key: "created_at"
        },
        {
          title: "",
          width: "190",
          align: "center",
          key: "updated_at"
        },
        {
          title: "",
          key: "operation",
          align: "center",
          width: "250",
          render: (h, params) => {
            return h("div", this.$renderBtn(h, params, this.BtnData));
          }
        }
      ],
      RoleData: [], //
      RowRoleData: [], //
      BtnData: [], //
      AddModal: false,
      AddInfo: {
        pid: "",
        name: "",
        status: "",
        sort: ""
      },
      AddRules: {
        name: [{ required: true, message: "" }],
        sort: [{ required: true, message: "" }]
      },
      EditModal: false,
      EditInfo: {
        id: "",
        name: "",
        status: "",
        sort: ""
      },
      AssignModal: false,
      AssignInfo: {
        role_id: "",
        auth_id: [0]
      },
      AuthList: []
    };
  },
  created() {
    this.InitData();
  },
  methods: {
    //
    InitData(callback = () => {}) {
      const that = this;
      const auth_id = this.$getLocal("auth_id");
      this.loading = true;
      //
      this.$post("/backend/Auth/roleList").then(d => {
        let info = d.data;
        that.RowRoleData = info;
        info.forEach(infoObj => {
          infoObj.status = infoObj.status ? "" : "";
        });
        that.RoleData = info;
        that.loading = false;
      });
      //
      this.$post("/backend/Auth/authList").then(d => {
        that.AuthList = d.data;
      });
      //
      this.$fetch("/backend/Menuauth/listAuthGet", { auth_id }).then(d => {
        this.BtnData = d.data.operation;
      });
    },
    //
    RefreshList() {
      this.InitData(() => {
        this.$Message.success("");
      });
    },
    //
    UploadData(url, info) {
      if (info.status) {
        info.status = info.status === "" ? 1 : 0;
      }
      return new Promise(resolve => {
        this.$post(url, info)
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve();
              this.InitData();
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    AddShow() {
      this.$refs["AddInfo"].resetFields();
      this.AddInfo.pid = 0;
      this.AddInfo.name = "";
      this.AddInfo.status = "";
      this.AddInfo.sort = "";
      this.AddModal = true;
    },
    //
    AddCancel() {
      this.AddModal = false;
    },
    AddOver() {
      this.$refs["AddInfo"].validate(valid => {
        if (valid) {
          this.UploadData("/backend/Auth/roleAdd", this.AddInfo).then(() => {
            this.AddModal = false;
          });
        }
      });
    },
    //
    EditOpt(row) {
      this.$refs["EditInfo"].resetFields();
      this.EditModal = true;
      for (let key in this.EditInfo) {
        if (key === "name") {
          this.EditInfo["name"] = row["role_name"];
        } else {
          this.EditInfo[key] = row[key];
        }
      }
    },
    //
    EditCancel() {
      this.EditModal = false;
    },
    EditOver() {
      this.$refs["EditInfo"].validate(valid => {
        if (valid) {
          let info = {
            id: "",
            name: "",
            status: "",
            sort: ""
          };
          this.TransData(this.EditInfo, info);
          this.UploadData("/backend/Auth/roleEdit", info).then(() => {
            this.EditModal = false;
          });
        }
      });
    },
    //
    AssignAuth(row) {
      this.AssignInfo.role_id = row.id;
      if (row.auth !== null && row.auth !== true) {
        let auth_id = row.auth.split(",");
        auth_id = auth_id.map(val => parseInt(val));
        this.AssignInfo.auth_id = auth_id;
      } else {
        this.AssignInfo.auth_id = [];
      }
      this.AssignModal = true;
    },
    AssignOver() {
      let info = {
        role_id: this.AssignInfo.role_id,
        auth_id: this.AssignInfo.auth_id.join(",")
      };
      this.UploadData("/backend/Auth/roleAuthAllotment", info);
    },
    //data
    TransData(obdata, normal) {
      for (let key in normal) {
        normal[key] = obdata[key];
      }
    },
    //
    Delopt(row) {
      //
      this.$Modal.confirm({
        title: "",
        content: '<p class="confirm-text"></p>',
        onOk: () => {
          this.UploadData("/backend/Auth/roleDel", { id: row.id });
        }
      });
    }
  }
};
</script>


<style lang="less" scoped>
.list-title {
  font-weight: normal;
}
.operate-box {
  padding: 10px 0 20px;
}
.auth-seclist {
  padding-left: 8px;
  display: flex;
  flex-direction: row;
}
.auto-area {
  max-height: 400px;
  overflow-y: auto;
}
.inline-block {
  display: inline-block;
}
</style>



