<template>
  <div>
    <Modal v-model="model1" title="" @on-ok="ok" @on-cancel="cancel">
      <Row>
        <Col span="12">   {{data.order_id}}</Col>
        <Col span="12">   {{data.name}}</Col>
        <Col span="12">   {{data.phone}}</Col>
        <Col span="12">  {{this.$store.getters.orderStatus[data.status]}}</Col>
        <Col span="12"> {{data.loan_day}}</Col>
        <!-- <Col span="12">
          <div v-if="data.channel==0"> app</div>
          <div v-else> </div>
        </Col> -->
        <Col span="12">{{data.anount_time}}</Col>
        <Col span="12"> {{data.created_at}}</Col>
        <Col span="12">{{data.return_time}}</Col>
        <Col span="12">{{data.repay_time}}</Col>

        <Col span="12"> {{data.review_time}}</Col>

        <Col span="12"> {{data.amount_due}}</Col>
        
        <Col span="12"> {{data.loan_time}}</Col>
        <Col span="12"> {{data.loan_amount}}</Col>
        <Col span="12"> {{data.pay_time}}</Col>

        <Col span="12"> {{data.actual_arrival}}</Col>
        
        <Col span="12"> {{data.auditor_name}}</Col>
        <Col span="12"> {{data.fee}}</Col>
        <Col span="12"> {{data.recruiter_name}}</Col>
        <Col span="12"> {{data.interest}}</Col>
        
      </Row>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "OrderModal",
  data() {
    return {
      model1: this.modalShow
    };
  },
  props: ["modalShow", "data"],
  watch: {
    modalShow(val) {
      this.model1 = val;
    }
  },
  methods: {
    cancel() {
      this.$emit("CloseModal", false);
    },
    ok() {
      this.$emit("CloseModal", false);
    }
  }
};
</script>




