<template>
  <div>
    <Modal v-model="model1" title="" @on-ok="ok" @on-cancel="cancel">
      <Row>
        <Col span="12">   {{data.order_id}}</Col>
        <Col span="12">   {{data.true_name}}</Col>
        <Col span="12">   {{data.phone}}</Col>
        <Col span="12"> {{data.order_amount}}</Col>
        <Col span="12">  {{data.number}}</Col>

        <Col span="12">  {{data.delay_amount}}</Col>
        <Col span="12">  {{data.fee}}</Col>
        <Col span="12">  {{data.days}}</Col>
        <Col span="12">  {{data.delay_begin_time}}</Col>
        <Col span="12">  {{data.delay_end_time}}</Col>
        <Col span="12">  {{data.overdue_days}}</Col>

        <Col span="12">  {{data.violate_amount}}</Col>
        <Col span="12">
           
          <span v-show="data.status==0"></span>
          <span v-show="data.status==1"></span>
          <span v-show="data.status==2"></span>
        </Col>
        <Col span="12">  {{data.update_at}}</Col>
      </Row>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "ExtensionModal",
  data() {
    return {
      model1: this.modalShow
    };
  },
  props: ["modalShow", "data"],
  watch: {
    modalShow(val) {
      this.model1 = val;
    }
  },
  methods: {
    cancel() {
      this.$emit("CloseModal", false);
    },
    ok() {
      this.$emit("CloseModal", false);
    }
  }
};
</script>




