<template>
  <div id="listRole">
    <h1 class="list-title">{{ title }}</h1>
    <div class="operate-box">
      <Button type="primary" icon="plus" size="large" @click="AddOpt(0)"></Button>
      <Button type="warning" icon="ios-reload" size="large" @click="RefreshList"></Button>
    </div>
    <div class="data-area">
      <Table border :columns="UserCol" size="large" :data="DepData" :loading="loading"></Table>
    </div>
    <Modal v-model="AddModal" title="">
      <Form
        ref="AddInfo"
        :model="AddInfo"
        :rules="ValidateRule"
        label-position="right"
        :label-width="80"
      >
        <!--<FormItem label="">
          <Input v-model="AddInfo.pid" disabled></Input>
        </FormItem>-->
        <FormItem label="" prop="name">
          <Input v-model="AddInfo.name"></Input>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="AddInfo.sort"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="AddCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
    <Modal v-model="EditModal" title="">
      <Form
        ref="EditInfo"
        :model="EditInfo"
        label-position="right"
        :rules="ValidateRule"
        :label-width="80"
      >
        <FormItem label="">
          <Input v-model="EditInfo.id" disabled></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model="EditInfo.name"></Input>
        </FormItem>
        <FormItem label="" prop="sort">
          <Input v-model="EditInfo.sort"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="EditCancel" size="large"></Button>
        <Button type="primary" @click="EditOver" size="large"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getLocal } from "@/util/util";
export default {
  name: "listRole",
  data() {
    return {
      title: "",
      loading: true,
      UserCol: [
        {
          title: "",
          align: "center",
          key: "id"
        },
        {
          title: "",
          align: "center",
          key: "pid"
        },
        {
          title: "",
          align: "center",
          key: "title"
        },
        {
          title: "",
          align: "center",
          key: "sort"
        },
        {
          title: "",
          align: "center",
          key: "status",
          render: (h, params) => {
            return params.row.status == 1 ? "" : "";
          }
        },
        {
          title: "",
          key: "operation",
          align: "center",
          width: "220",
          render: (h, params) => {
            return h("div", this.$renderBtn(h, params, this.BtnData));
          }
        }
      ],
      DepData: [], //
      RowDepData: [], //
      BtnData: [], //
      ValidateRule: {
        name: [{ required: true, message: "" }],
        sort: [{ required: true, message: "" }]
      },
      EditModal: false,
      EditInfo: {
        id: "",
        name: "",
        sort: ""
      },
      AddModal: false,
      AddInfo: {
        pid: "",
        name: "",
        sort: ""
      },
      AssignModal: false,
      AssignInfo: {
        role_id: "",
        auth_id: [""]
      },
      AuthList: []
    };
  },
  created() {
    this.InitData();
  },
  methods: {
    //
    InitData(callback = () => {}) {
      const that = this;
      const auth_id = getLocal("auth_id");
      this.loading = true;
      //
      this.$post("/backend/Auth/deparList").then(d => {
        let info = d.data;
        let res = [];
        that.RowDepData = info;
        info.forEach(item => {
          res.push(item);
          if (item.auth.length > 0) {
            item.auth.map(val => {
              val.title = "|" + val.title;
            });
            res.push(...item.auth);
          }
        });
        this.DepData = res;
        that.loading = false;
        callback();
      });
      //
      this.$fetch("/backend/Menuauth/listAuthGet", { auth_id }).then(d => {
        this.BtnData = d.data.operation;
      });
    },
    //
    RefreshList() {
      this.InitData(() => {
        this.$Message.success("");
      });
    },
    //
    UploadData(url, info) {
      return new Promise(resolve => {
        this.$post(url, info)
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve();
              this.InitData();
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    AddOpt(p) {
      this.$refs["AddInfo"].resetFields();
      for (let key in this.AddInfo) {
        this.AddInfo[key] = "";
      }
      this.AddInfo.pid = p.pid;
      this.AddModal = true;
    },
    AddCancel() {
      this.AddModal = false;
    },
    //
    AddOver() {
      this.$refs["AddInfo"].validate(valid => {
        if (valid) {
          this.UploadData("/backend/Auth/deparAdd", this.AddInfo).then(() => {
            this.AddModal = false;
          });
        }
      });
    },
    //
    EditOpt(row) {
      this.EditModal = true;
      for (let key in this.EditInfo) {
        if (key === "name") {
          this.EditInfo["name"] = row["title"];
        } else {
          this.EditInfo[key] = row[key];
        }
      }
    },
    EditCancel() {
      this.EditModal = false;
    },
    //
    EditOver() {
      this.$refs["EditInfo"].validate(valid => {
        if (valid) {
          this.UploadData("/backend/Auth/deparUp", this.EditInfo).then(() => {
            this.EditModal = false;
          });
        }
      });
    },
    //
    ChangeStatus(row) {
      return new Promise(resolve => {
        this.$post("/backend/Auth/disableDepartment", { id: row.id })
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve();
              this.InitData();
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    Delopt(p) {
      //
      this.$Modal.confirm({
        title: "",
        content: '<p class="confirm-text"></p>',
        onOk: () => {
          this.UploadData("/backend/Auth/deparDel", { id: p.id });
        }
      });
    }
  }
};
</script>


<style lang="less" scoped>
.list-title {
  font-weight: normal;
}
.operate-box {
  padding: 10px 0 20px;
}
</style>



