<template>
  <div id="user-config">
    <h1 class="list-title">
      <span class="tit-text">{{ title }}</span>
    </h1>
    <div class="data-area" style="margin-bottom: 20px;">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <div class="opt-box">
            <h3>
              <Icon type="images"></Icon>
            </h3>
            <Poptip placement="right">
              <h4 class="ml-10">
                <Icon type="ios-help"/>
              </h4>
              <div class="api" slot="content">
                <p>=+**+</p>
                <p>=+*</p>
                <p>=**</p>
                <p> < </p>
                <p> >= </p>
              </div>
            </Poptip>
          </div>
          <div class="btn-box">
            <Button type="warning" size="large" icon="plus-circled" @click="AddRate"></Button>
          </div>
        </div>
        <Table border :columns="RateCol" :data="RateData" :loading="Rateloading"></Table>
      </Card>
    </div>
    <Modal v-model="RateModal.modal" title="" :width="600">
      <Form
        ref="RateModal"
        :model="RateModal.data"
        :rules="RateRules"
        label-position="right"
        :label-width="110"
      >
        <FormItem label="" prop="rate_name">
          <Input v-model="RateModal.data.rate_name"></Input>
        </FormItem>
        <FormItem label="" prop="borrowing_rate">
          <Input v-model="RateModal.data.borrowing_rate"></Input>
        </FormItem>
        <FormItem label="" prop="rollover_rate">
          <Input v-model="RateModal.data.rollover_rate"></Input>
        </FormItem>
        <FormItem label="" prop="overdue_rate">
          <Input v-model="RateModal.data.overdue_rate"></Input>
        </FormItem>
        <FormItem label="" prop="daily_rate">
          <Input v-model="RateModal.data.daily_rate"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="handleReset('RateModal')" size="large"></Button>
        <Button type="primary" @click="handleSubmit('RateModal')" size="large"></Button>
      </div>
    </Modal>
    <div class="data-area">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="clipboard"></Icon>
          </h3>
          <div class="btn-box">
            <Button type="warning" size="large" icon="wrench" @click="AddModeModal"></Button>
          </div>
        </div>
        <Table border :columns="UserCol" :data="UserData" :loading="loading"></Table>
      </Card>
    </div>

    <Modal v-model="ModeModal.modal" title="">
      <Form
        ref="ModeModal"
        :model="ModeModal.data"
        :rules="ValidateRules"
        label-position="right"
        :label-width="100"
      >
        <FormItem label="" prop="level">
          <Input v-model="ModeModal.data.level"/>
        </FormItem>
        <FormItem label="" prop="money_limit">
          <Input v-model="ModeModal.data.money_limit"/>
        </FormItem>
        <FormItem label="" prop="days">
          <Input v-model="ModeModal.data.days"/>
        </FormItem>
        <FormItem label="" prop="config_rate_id">
          <Select v-model="ModeModal.data.config_rate_id" style="width:200px">
            <Option
              v-for="(item,index) in RateData"
              :key="index"
              :value="item.id"
            >{{ item.rate_name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="is_default">
          <Select v-model="ModeModal.data.is_default" style="width:200px">
            <Option v-for="(item,index) in moren" :key="index" :value="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
        <!-- <FormItem label="">
          <Input v-model="ModeModal.data.remark" type="textarea" :rows="3"></Input>
        </FormItem>-->
      </Form>
      <div slot="footer">
        <Button type="text" @click="ModalCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getLocal } from "@/util/util";
import radioList from "@/components/list/radioList";
export default {
  name: "UserConfig",
  comments: {
    radioList
  },
  data() {
    const validateRate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (rule.trigger == "jk") {
        if (isNaN(value) || value > 0.3) {
          callback(new Error("0.3"));
        }
        callback();
      } else if (rule.trigger == "zq") {
        if (isNaN(value) || value > 0.35) {
          callback(new Error("0.35"));
        }
        callback();
      } else if (rule.trigger == "yq") {
        if (isNaN(value) || value > 0.1) {
          callback(new Error("0.1"));
        }
        callback();
      } else if (rule.trigger == "rl") {
        if (isNaN(value) || value > 0.06) {
          callback(new Error("0.06"));
        }
        callback();
      }
    };
    return {
      moren: [{ id: 0, name: "" }, { id: 1, name: "" }],
      title: "",
      apiUrl: "/backend/Sysconfig/sysLevelList",
      auth_id: "",
      loading: true,
      Rateloading: false,
      TextArr: {
        status: ["", ""]
      },
      //
      RateModal: {
        modal: false,
        isEdit: false,
        id: "",
        data: {
          rate_name: "",
          borrowing_rate: "",
          rollover_rate: "",
          overdue_rate: "",
          daily_rate: ""
        }
      },
      RateBtnData: [
        {
          name: "",
          color: "primary",
          class: "EditRate"
        }
        // {
        //   name: '/',
        //   color: 'warning',
        //   class: 'ChangeRate'
        // }
      ],
      RateRules: {
        rate_name: [
          { required: true, message: "", trigger: "change" }
        ],
        borrowing_rate: [{ validator: validateRate, trigger: "jk" }],
        rollover_rate: [{ validator: validateRate, trigger: "zq" }],
        overdue_rate: [{ validator: validateRate, trigger: "yq" }],
        daily_rate: [{ validator: validateRate, trigger: "rl" }]
      },
      RateData: [],
      //
      ModeModal: {
        modal: false,
        isEdit: false,
        id: "",
        data: {
          level: "",
          days: "",
          config_rate_id: "",
          remark: "",
          status: "",
          is_default: ""
        }
      },
      UserCol: [
        // {
        //   title: "",
        //   width: "70",
        //   align: "center",
        //   key: "id"
        // },
        {
          title: "",
          align: "center",
          key: "level"
        },
        {
          title: "",
          align: "center",
          key: "days"
        },
        {
          title: "",
          align: "center",
          key: "money_limit"
        },
        {
          title: "",
          align: "center",
          key: "rate_name"
        },
        {
          title: "",
          align: "center",
          key: "is_default",
          render: (h, params) => {
            if (params.row.is_default == 0) {
              return "";
            } else {
              return "";
            }
          }
        },
        // {
        //   title: "",
        //   align: "center", key: "remark"
        // },
        // {

        // {
        //   title: "",
        //   key: "state",
        //   align: "center",
        //   render: (h, params) => {
        //     return h("div", [
        //       h(radioList, {
        //         props: {
        //           status: params.row.status,
        //           id: params.row.id,
        //           index: params.index,
        //           datas: this.UserData,
        //           url: "/backend/Sysconfig/sysLevelDisable"
        //         }
        //       })
        //     ]);
        //   }
        // },
        {
          title: "",
          key: "operation",
          align: "center",
          render: (h, params) => {
            return h("div", this.$renderBtn(h, params, this.BtnData));
          }
        }
      ],
      RateCol: [
        // {
        //   title: "",
        //   width: "70",
        //   align: "center",
        //   key: "id"
        // },
        {
          title: "",
          align: "center",
          key: "rate_name"
        },
        {
          title: "",
          align: "center",
          key: "borrowing_rate"
        },
        {
          title: "",
          align: "center",
          key: "rollover_rate"
        },
        {
          title: "",
          align: "center",
          key: "overdue_rate"
        },
        {
          title: "",
          align: "center",
          key: "daily_rate"
        },

        // {
        //   title: "",
        //   key: "operation",
        //   align: "center",
        //   render: (h, params) => {
        //     return h("div", [
        //       h(radioList, {
        //         props: {
        //           status: params.row.status,
        //           id: params.row.id,
        //           index: params.index,
        //           datas: this.UserData,
        //           url: "/backend/rate/disable"
        //         }
        //       })
        //     ]);
        //   }
        // },
        {
          title: "",
          key: "operation",
          align: "center",
          render: (h, params) => {
            return h("div", this.$renderBtn(h, params, this.RateBtnData));
          }
        }
      ],
      ValidateRules: {
        config_rate_id: [{ required: true, message: "" }],
        money_limit: [{ required: true, message: "" }],
        level: [{ required: true, message: "" }],
        is_default: [{ required: true, message: "" }],
        days: [{ required: true, message: "" }]
      },
      UserData: [], //
      RowUserData: [], //
      BtnData: []
    };
  },
  created() {
    this.auth_id = getLocal("auth_id");
    this.InitData(this.apiUrl);
    this.getConfigRate();
  },
  methods: {
    // ChangeRate(row){
    //   //
    //   this.$fetch('/backend/rate/disable',{id: row.id}).then((d)=>{
    //     if(d.status === 1){
    //         this.$Message.success(d.message);
    //         this.getConfigRate();
    //       }else{
    //         this.$Message.error('');
    //       }
    //   });
    // },
    EditRate(row) {
      this.RateModal.modal = true;
      this.RateModal.isEdit = true;
      this.RateModal.data.id = row.id;
      this.RateModal.data.rate_name = row.rate_name;
      this.RateModal.data.borrowing_rate = row.borrowing_rate;
      this.RateModal.data.rollover_rate = row.rollover_rate;
      this.RateModal.data.overdue_rate = row.overdue_rate;
      this.RateModal.data.daily_rate = row.daily_rate;
    },
    //data
    RemoveObserve(rowdata) {
      return JSON.parse(JSON.stringify(rowdata));
    },
    AddRate() {
      this.$refs["RateModal"].resetFields();

      this.RateModal.isEdit = false;
      this.RateModal.modal = true;
    },
    //
    InitData(url, params = {}) {
      const that = this;
      this.loading = true;
      //
      this.$fetch("/backend/Menuauth/listAuthGet", {
        auth_id: this.auth_id
      }).then(d => {
        this.BtnData = d.data.operation;
      });
      //
      return new Promise(resolve => {
        this.$post(url, params).then(d => {
          let res = d.data;
          this.RowUserData = res;
          this.UserData = res;
          // if (res.length > 0) {
          //   this.UserData.forEach(val => {
          //     for (let key in val) {
          //       if (key in this.TextArr) {
          //         val[key] = this.TextArr[key][val[key]];
          //       }
          //     }
          //   });
          // }
          that.loading = false;
          resolve();
        });
      });
    },
    //
    UploadData(url, info) {
      return new Promise(resolve => {
        this.$post(url, info)
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve(d.data);
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    AddModeModal() {
      this.$refs["ModeModal"].resetFields();
      this.ModeModal.data = {
        level: "",
        money_limit: "",
        days: "",
        config_rate_id: "",
        remark: ""
      };
      this.ModeModal.isEdit = false;
      this.ModeModal.modal = true;
    },
    ModalCancel() {
      this.ModeModal.modal = false;
    },
    AddOver() {
      this.$refs["ModeModal"].validate(valid => {
        if (valid) {
          this.ModeModal.modal = false;
          let ninfo = this.RemoveObserve(this.ModeModal.data);
          const isEdit = this.ModeModal.isEdit;
          const url = isEdit
            ? "/backend/Sysconfig/sysLevelUp"
            : "/backend/Sysconfig/sysLevelAdd";
          if (isEdit) {
            ninfo.id = this.ModeModal.id;
          }
          this.UploadData(url, ninfo).then(() => {
            this.InitData(this.apiUrl);
          });
        }
      });
    },
    getConfigRate() {
      return new Promise(resolve => {
        this.$post("backend/rate/list", this.RateModal.data)
          .then(d => {
            if (d.status === 1) {
              this.RateData = d.data;
              resolve(d.data);
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.RateModal.modal = false;

          this.RateModal.data.admin_id = getLocal("admin_id");
          let url = this.RateModal.isEdit
            ? "backend/rate/edit"
            : "backend/rate/add";
          return new Promise(resolve => {
            this.$post(url, this.RateModal.data)
              .then(d => {
                if (d.status === 1) {
                  this.$Message.success(d.message);
                  this.getConfigRate();
                  resolve(d.data);
                } else {
                  this.$Message.error(d.message);
                }
              })
              .catch(err => {
                this.$Message.error("");
              });
          });
        }
      });
    },
    handleReset(name) {
      this.$refs[name].resetFields();
      this.RateModal.modal = false;
    },
    //
    EditOpt(row) {
      this.ModeModal.data = {
        level: row.level,
        money_limit: row.money_limit,
        days: row.days,
        config_rate_id: row.config_rate_id,
        remark: row.remark,
        is_default: row.is_default
      };
      this.ModeModal.isEdit = true;
      this.ModeModal.id = row.id;
      this.ModeModal.modal = true;
    },
    ChangeStatus(row) {
      this.$post("/backend/Sysconfig/sysLevelDisable", { id: row.id }).then(
        response => {
          this.$Message.success(response.message);
          this.InitData(this.apiUrl);
        }
      );
    },

    //
    Delopt(row) {
      this.$Modal.confirm({
        title: "",
        content: `<p class="confirm-text"></p>`,
        onOk: () => {
          this.UploadData("/backend/Sysconfig/sysLevelDel", {
            id: row.id
          }).then(() => {
            this.InitData(this.apiUrl);
          });
        }
      });
    }
  }
};
</script>


<style scoped>
.list-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: normal;
  padding-bottom: 10px;
}
.data-area {
  padding-top: 20px;
  .ivu-table-wrapper {
    border: none;
  }
  .page-box {
    padding: 20px;
    display: flex;
    flex-direction: row-reverse;
  }
}
.card-tit {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.col-center {
  text-align: center;
}
.auto-height {
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
}
.info-width {
  width: 200px;
}
.api > p {
  display: block;
  font-weight: normal;
  font-size: 12px;
  color: #495060;
}
.ml-10 {
  margin-left: 10px;
}
</style>



