<template>
  <div class="content">
    <Form :model="formItem" :label-width="180">
      <FormItem label=""></FormItem>
      <FormItem label="">
        <Input style="width: auto" type="text" v-model="formItem.money"></Input>
      </FormItem>
      <FormItem label>
        <span class="info">(5-10)</span>
      </FormItem>
      <FormItem label="">
        <div>
          <img src="../../assets/images/recharge.png" width="100px" alt>
        </div>
      </FormItem>
      <FormItem>
        <Button type="default"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  name: "rechargeIndex",
  data() {
    return {
      formItem: {}
    };
  }
};
</script>

<style scoped>
.content /deep/ .ivu-form-item-label {
  font-size: 20px;
}
.content /deep/ .ivu-form-item-content {
  font-size: 20px;
  padding-top: 5px;
}
.info {
  color: rgba(74, 74, 74, 0.7);
  font-size: 18px;
}
</style>



