import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'

Vue.use(Vuex);

const state = {
  username: '',
  currentpath: '',
  authView: [],
  firstview: '',
  chargeType: {
    // 1:'',
    // 2:'',
    // 3:''
    // 1: '', 2: '', 3: '', 4: '', 5: 'QQ', 6: '', 7: '', 8: '', 9: '', 10: '', 11: ''
    1: '', 2: '', 3: '', 4: '', 5: 'QQ', 6: '', 7: '', 8: '', 9: '', 10: '', 11: '',12:"",13:'',14:''
  },
  riskLevel:[
  {num:'0',name:'',title:'>3',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'2',name:'',title:'',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'5',name:'',title:'',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'8',name:'',title:'>=5>=10>=10>=5',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'10',name:'',title:'',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'11',name:'app',title:'app>=10',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'23',name:'',title:'3090',cls:'risk_yellow',time:'',class:'risk_yellow'},
  {num:'24',name:'',title:'030',cls:'risk_blue',time:'',class:'risk_blue'},
  {num:'6',name:'',title:'',cls:'risk_red',time:'',class:'risk_red'},
  {num:'7',name:'',title:'90',cls:'risk_red',time:'',class:'risk_red'},
    {num:'13',name:'',title:'',cls:'risk_red',time:'',class:'risk_red'},
    {num:'17',name:'',title:'',cls:'risk_red',time:'',class:'risk_red'},
    {num:'18',name:'',title:'',cls:'risk_red',time:'',class:'risk_red'},
    {num:'21',name:'',title:'',cls:'risk_red',time:'',class:'risk_red'},
    
],
  status: {
    1: '',
    2: ''
  },
  orderStatus: {
    // 1:'',
    // 2:'',
    // 3:'',
    // 4:'',
    // 5:'',
    // 6:'',
    // 8:'',
    // 9:''
    1: '',
    2: '',
    3: '',
    4: '',
    5: '',
    6: '',
    8: '',
    9: '',
    10: ''
  },
  payType: {
    //123
    1: '',
    2: '',
    3: '',
    4: ''
  }
};

const getters = {
  permission: state => {
    return state.authView;
  },
  chargeType: state => state.chargeType,
  status: state => state.status,
  orderStatus: state => state.orderStatus,
  riskLevel: state => state.riskLevel,
  payType: state => state.payType,
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



