<template>
  <Modal
    v-model="SmsState"
    title=""
    @on-ok="EditOver"
    @on-cancel="CloseBtn">
    <Form label-position="right" :label-width="120">
      <FormItem label="">
        <div class="object-info">
          <span class="important-num">{{CountNum}}</span>
          <p class="switch-box">
            <i-switch size="large" v-model="Result.all">
              <span slot="open"></span>
              <span slot="close"></span>
            </i-switch>
          </p>
        </div>
      </FormItem>
      <FormItem label="">
        <Select v-model="Result.type" style="width: 300px;"
                placeholder="">
          <Option :value="1"></Option>
          <Option :value="2"></Option>
          <Option :value="3"></Option>
          <Option :value="4"></Option>
        </Select>
      </FormItem>
      <FormItem label="">
        <span>{{CountNum}}</span><span class="important-num">100000</span>
      </FormItem>
    </Form>
  </Modal>
</template>

<script>
  export default {
    name: 'GroupSms',
    data () {
      return{
        SmsState: this.modalShow,
        Result: {
          id: this.InitData,
          type: 1,
          all: false
        }
      }
    },
    props: {
      modalShow: Boolean,
      InitData: Array,
      Count: Number
    },
    watch: {
      modalShow(val){
        this.SmsState = val;
      },
      InitData(val){
        this.Result.id = val;
      }
    },
    computed:{
      CountNum(){
          if(this.InitData.length > 0){
            if(this.Result.all){
              return this.Count;
            }else{
              return this.InitData.length;
            }
          }else{
            return 0;
          }
      }
    },
    methods: {
      EditOver(){
        this.$emit('UpOver',this.Result);
      },
      CloseBtn(){
        this.$emit('CloseModal',this.modalState);
      }
    }
  }
</script>

<style lang="less" scoped>
  .important-num{
    color: #f74c17;
  }
  .object-info{
    display: flex;
    flex-direction: row;
    .switch-box{
      margin-left: 20px;
    }
  }
</style>



