<template>
  <div id="auto-post">
    <h1 class="list-title">
      <span class="tit-text">{{ title }}</span>
      <Button class="tit-btn" type="info" icon="ios-reload" size="large" @click="RefreshList"></Button>
    </h1>
    <div class="screen-area">
      <Card>
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="ios-pricetags-outline"></Icon>
          </h3>
          <div class="btn-box">
            <Button type="ghost" icon="reply" @click="ResetScreen"></Button>
            <Button type="success" icon="search" @click="SimpleSearch"></Button>
          </div>
        </div>
        <div class="opt-box">
          <Form :model="ScreenData" inline :label-width="85">
            <FormItem label="">
              <Input v-model="ScreenData.name" style="width: 120px"></Input>
            </FormItem>
            <FormItem label="">
              <Select v-model="ScreenData.status" style="width: 160px">
                <Option value="1"></Option>
                <Option value="2">APP</Option>
              </Select>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
    <div class="data-area">
      <Card :padding="0">
        <div class="card-tit" slot="title">
          <h3>
            <Icon type="clipboard"></Icon>
          </h3>
          <div class="btn-box">
            <Button type="warning" size="large" icon="chatbox" @click="AddRulesModal"></Button>
          </div>
        </div>
        <Table
          border
          :columns="UserCol"
          :data="UserData"
          :loading="loading"
          @on-selection-change="SelectTable"
        ></Table>
      </Card>
    </div>
    <Modal v-model="RulesModal.modal" title="">
      <Form
        ref="RulesModal"
        :model="RulesModal.data"
        :rules="ValidateRules"
        label-position="right"
        :label-width="80"
        class="auto-height"
      >
        <FormItem label="">
          <RadioGroup v-model="RulesModal.data.disable">
            <Radio :label="0"></Radio>
            <Radio :label="1"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="RulesModal.data.sms_type">
            <Radio :label="0">app</Radio>
            <Radio :label="1"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" porp="type">
          <Select v-model="RulesModal.data.type" class="info-width">
            <Option :value="0"></Option>
            <Option :value="1"></Option>
            <Option :value="2"></Option>
            <Option :value="3"></Option>
          </Select>
        </FormItem>
        <FormItem label="" v-show="BehaviorData.show" prop="behavior">
          <Select v-model="RulesModal.data.behavior" class="info-width">
            <Option v-for="(opt, index) in BehaviorArr" :value="index" :key="opt">{{opt}}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="status">
          <Select v-model="RulesModal.data.status" class="info-width">
            <Option v-for="(opt, index) in StatusArr" :value="index" :key="opt">{{opt}}</Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <RadioGroup v-model="RulesModal.data.period">
            <Radio v-for="(opt, index) in TriggerArr" :label="index" :key="opt">{{opt}}</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="trigger_date">
          <Row>
            <Col span="3">
              <Input :number="true" v-model="RulesModal.data.trigger_date"></Input>
            </Col>
            <Col span="2" class="col-center"></Col>
            <!--<Col span="3" v-show="IsUrge">
              <Input :number="true" v-model="RulesModal.data.hour"></Input>
            </Col>
            <Col span="2" class="col-center" v-show="IsUrge"></Col>
            <Col span="3" v-show="IsUrge">
            <Input :number="true" v-model="RulesModal.data.min"></Input>
            </Col>
            <Col span="2" class="col-center" v-show="IsUrge"></Col>
            <Col span="3" v-show="IsUrge">
            <Input :number="true" v-model="RulesModal.data.sec"></Input>
            </Col>
            <Col span="2" class="col-center" v-show="IsUrge"></Col>-->
          </Row>
        </FormItem>
        <FormItem label="" prop="title">
          <Input v-model="RulesModal.data.title"></Input>
        </FormItem>
        <FormItem label="" prop="content">
          <Input
            v-model="RulesModal.data.content"
            type="textarea"
            :autosize="{minRows: 3,maxRows: 5}"
          ></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" @click="ModalCancel" size="large"></Button>
        <Button type="primary" @click="AddOver" size="large"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getLocal } from "@/util/util";

export default {
  name: "AutoPost",
  data() {
    return {
      title: "",
      apiUrl: "/backend/Autopush/autoPushList",
      auth_id: "",
      loading: true,
      TextArr: {
        trigger: ["", "", ""],
        display: ["", ""],
        status: ["", "", "", ""],
        type: ["", "", "", ""],
        sms_type: ["app", ""]
      },
      //
      BehaviorData: {
        show: true,
        type: [
          [""],
          ["", ""],
          ["", "", "", ""],
          []
        ]
      },
      StatusData: [
        ["", ""],
        ["", ""],
        ["", ""],
        ["", ""]
      ],
      TriggerData: {
        single: [""],
        double: ["", ""]
      },
      //
      ScreenData: {
        name: "",
        status: ""
      },
      //
      RulesModal: {
        modal: false,
        isEdit: false,
        id: "",
        data: {
          sms_type: 0,
          type: 0,
          behavior: 0,
          status: 0,
          trigger: 0,
          trigger_date: 0,
          disable: 1,
          title: "",
          content: ""
        }
      },
      UserCol: [
        {
          type: "selection",
          width: 55,
          align: "center"
        },
        {
          title: "",
          width: "70",
          align: "center",
          key: "id"
        },
        {
          title: "",
          width: "100",
          align: "center",
          key: "title"
        },
        {
          title: "",
          align: "center",
          key: "status"
        },
        {
          title: "",
          align: "center",
          key: "behavior"
        },
        {
          title: "",
          align: "center",
          key: "type"
        },
        {
          title: "",
          align: "center",
          key: "sms_type"
        },
        {
          title: "",
          align: "center",
          key: "trigger"
        },
        {
          title: "()",
          align: "center",
          key: "trigger_date"
        },
        {
          title: "",
          key: "operation",
          align: "center",
          width: "330",
          render: (h, params) => {
            let rule = {};
            rule.btns = [
              {
                class: "ChangeStatus",
                name: "disable",
                type: "warning",
                right: "",
                wbtn: ""
              }
            ];
            rule.size = 2;
            return h("div", this.$renderBtn(h, params, this.BtnData, rule));
          }
        }
      ],
      UserData: [], //
      RowUserData: [], //
      BtnData: [],
      //
      ValidateRules: {
        type: [{ required: true, message: "" }],
        behavior: [{ required: true, message: "" }],
        status: [{ required: true, message: "" }],
        trigger_date: [{ required: true, message: "" }],
        title: [{ required: true, message: "" }],
        content: [{ required: true, message: "" }]
      },
      //
      SelectData: []
    };
  },
  created() {
    this.auth_id = getLocal("auth_id");
    this.InitData(this.apiUrl);
  },
  computed: {
    BehaviorArr() {
      this.BehaviorData.show = this.RulesModal.data.type === 3 ? false : true;
      return this.BehaviorData.type[this.RulesModal.data.type];
    },
    StatusArr() {
      return this.StatusData[this.RulesModal.data.type];
    },
    TriggerArr() {
      if (this.RulesModal.data.type === 3) {
        return this.TriggerData.double;
      } else {
        return this.TriggerData.single;
      }
    },
    IsUrge() {
      const type = this.RulesModal.data.type;
      if (type === 3) {
        this.RulesModal.data.day = 0;
        this.RulesModal.data.hour = 0;
        this.RulesModal.data.min = 0;
        this.RulesModal.data.sec = 0;
      }
      return type === 3 ? false : true;
    }
  },
  methods: {
    //data
    RemoveObserve(rowdata) {
      return JSON.parse(JSON.stringify(rowdata));
    },
    //()
    ResetScreen() {
      for (let key in this.ScreenData) {
        this.ScreenData[key] = "";
      }
      this.allTime = "";
    },
    //
    SelectTable(data) {
      let idarr = [];
      if (data.length > 0) {
        data.forEach(val => {
          idarr.push(val.id);
        });
      }
      this.SelectData = idarr;
    },
    //
    SimpleSearch(sign = 1) {
      let sinfo = this.RemoveObserve(this.ScreenData);
      this.InitData(this.apiUrl, sinfo).then(() => {
        if (sign) {
          this.$Message.success("");
        }
      });
    },
    //
    InitData(url, params = {}) {
      const that = this;
      this.loading = true;
      //
      this.$fetch("/backend/Menuauth/listAuthGet", {
        auth_id: this.auth_id
      }).then(d => {
        this.BtnData = d.data.operation;
      });
      //
      return new Promise(resolve => {
        this.$post(url, params).then(d => {
          let res = d.data;
          this.RowUserData = res;
          this.UserData = res;
          if (res.length > 0) {
            this.UserData.forEach(val => {
              for (let key in val) {
                if (key in this.TextArr) {
                  val[key] = this.TextArr[key][val[key]];
                }
              }
            });
          }
          that.loading = false;
          resolve();
        });
      });
    },
    //
    RefreshList() {
      this.InitData("/backend/Collection/CollectionList").then(() => {
        this.$Message.success("");
      });
    },
    //
    UploadData(url, info) {
      return new Promise(resolve => {
        this.$post(url, info)
          .then(d => {
            if (d.status === 1) {
              this.$Message.success(d.message);
              resolve(d.data);
            } else {
              this.$Message.error(d.message);
            }
          })
          .catch(err => {
            this.$Message.error("");
          });
      });
    },
    //
    AddRulesModal() {
      this.$refs["RulesModal"].resetFields();
      this.RulesModal.data = {
        sms_type: 0,
        type: 0,
        behavior: 0,
        status: 0,
        trigger: 0,
        trigger_date: 0,
        disable: 1,
        title: "",
        content: "",
        day: 0,
        hour: 0,
        min: 0,
        sec: 0
      };
      this.RulesModal.isEdit = false;
      this.RulesModal.modal = true;
    },
    ModalCancel() {
      this.RulesModal.modal = false;
    },
    AddOver() {
      this.$refs["RulesModal"].validate(valid => {
        if (valid) {
          let ninfo = this.RemoveObserve(this.RulesModal.data);
          const isEdit = this.RulesModal.isEdit;
          const url = isEdit
            ? "/backend/Autopush/autoPushUp"
            : "/backend/Autopush/autoPushAdd";
          if (isEdit) {
            ninfo.id = this.RulesModal.id;
          }
          this.UploadData(url, ninfo).then(() => {
            this.RulesModal.modal = false;
            this.InitData(this.apiUrl);
          });
        }
      });
    },
    //
    ChangeStatus(row) {
      this.UploadData("/backend/Autopush/autoPushDisable", { id: row.id }).then(
        () => {
          this.SimpleSearch(0);
        }
      );
    },
    //
    EditOpt(row) {
      this.$post("backend/Autopush/autoPushReqUp", { id: row.id }).then(d => {
        this.RulesModal.data = {
          sms_type: d.data.sms_type,
          type: d.data.type,
          behavior: d.data.behavior,
          status: d.data.status,
          trigger: d.data.trigger,
          trigger_date: d.data.trigger_date,
          disable: d.data.disable,
          title: d.data.title,
          content: d.data.content
        };
        this.RulesModal.isEdit = true;
        this.RulesModal.id = row.id;
        this.RulesModal.modal = true;
      });
    },
    //
    Delopt(row) {
      this.$Modal.confirm({
        title: "",
        content: `<p class="confirm-text"></p>`,
        onOk: () => {
          this.UploadData("/backend/Autopush/autoPushDel", { id: row.id }).then(
            () => {
              this.SimpleSearch(0);
            }
          );
        }
      });
    }
  }
};
</script>


<style lang="less" scoped>
.list-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: normal;
  padding-bottom: 10px;
}
.data-area {
  padding-top: 20px;
  .ivu-table-wrapper {
    border: none;
  }
  .page-box {
    padding: 20px;
    display: flex;
    flex-direction: row-reverse;
  }
}
.card-tit {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.col-center {
  text-align: center;
}
.auto-height {
  max-height: 400px;
  overflow-y: auto;
  overflow-x: hidden;
}
.info-width {
  width: 200px;
}
</style>



