<template>
  <div class="carrier-report">
    <h2 class="all-title"></h2>
      <div class="first-level">
        <table class="table-info" cellspacing="0" cellpadding="0" border="0">
          <thead>
          <tr>
            <th colspan="5" class="cus-head">1.1 </th>
          </tr>
          </thead>
          <tbody>
            <tr>
              <td>{{ UserBasic.name }}</td>
              <td colspan="3">{{ UserBasic.id_card }}</td>
            </tr>
            <tr>
              <td>From{{UserBasic.name}}</td>
              <td colspan="3">From{{UserBasic.name}}</td>
            </tr>
            <tr>
              <td>{{}}</td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td colspan="2"></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td colspan="4"></td>
            </tr>
            <tr>
              <td colspan="2"> | </td>
              <td colspan="2"></td>
            </tr>
            <tr>
              <td colspan="4">[180][39][62.12][1]</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="first-level">
        <table class="table-info long" cellspacing="0" cellpadding="0" border="0">
          <thead>
          <tr>
            <th colspan="32" class="cus-head"></th>
          </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td>5:30-11:30</td>
              <td>5:30-11:30</td>
              <td>11:30-13:30</td>
              <td>11:30-13:30</td>
              <td>11:30-13:30</td>
              <td>13:30-17:30</td>
              <td>17:30-23:30</td>
              <td>17:30-23:30</td>
              <td>23:30-5:30</td>
              <td>23:30-5:30</td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
  </div>
</template>

<script>
  export default {
    name: 'SumTable',
    data () {
      return{
        Report:{
          user_basic: []
        }
      }
    },
    computed:{
      UserBasic(){
        let info = {};
        this.Report.user_basic.forEach(val=>{
          info[val.key] = val.value;
        });
        return info;
      }
    },
    created(){
      const uid = this.$route.query.uid;
      this.$fetch('/backend/report/carrier',{user_id: uid}).then(d=>{
        if(d.status === 1){
          this.Report = d.data.data;
        }
      });
    },
    methods: {
      JudgeSum(key){
        if(key === 'sum'){
          return '';
        }else{
          return key;
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .all-title{
    text-align: center;
    font-size: 24px;
    padding: 15px 0;
  }
  .carrier-report{
    width: 100%;
    box-sizing: border-box;
    padding: 0 5%;
    .first-level{
      padding-bottom: 20px;
      width: 100%;
      overflow: auto;
      .table-info{
        margin-bottom: 20px;
        &.long{
          width: 7000px;
        }
      }
    }
    .first-title{
      padding: 10px;
      font-size: 16px;
    }
    table{
      width: 100%;
      font-size: 12px;
      text-align: left;
      border: 1px solid #e3e3e3;
      border-bottom: none;
      .cus-head{
        height: 40px;
        white-space: nowrap;
        padding-left: 10px;
        background: #468cb4;
        font-size: 14px;
        overflow: hidden;
        color: #FFF;
        border-bottom: 1px solid #e9eaec;
      }
      tbody{
        tr{
          td{
            min-width: 0;
            height: 48px;
            padding-left: 10px;
            font-size: 14px;
            box-sizing: border-box;
            text-overflow: ellipsis;
            border-bottom: 1px solid #e9eaec;
            border-right: 1px solid #e3e3e3;
            &.td-title{
              font-weight: bold;
            }
            &:nth-last-child(1){
              border-right: none;
            }
            &.sum-left{
              border-right: 1px solid #e9eaec;
            }
            .td-name{
              font-size: 15px;
            }
            .td-amount{
              font-size: 14px;
              color: #d73435;
            }
          }
        }
      }
    }
  }
</style>



