<template>
  <div id="contract-info">
    <a href="javascript:;" class="printBtn" @click="PrintFunc">
      <Icon type="printer"></Icon>
      
    </a>
    <h1 class="all-title"></h1>
    <p class="bold">{{ UserData.username }}</p>
    <p>{{ UserData.card_type }}</p>
    <p class="division">{{ UserData.cardno }}</p>
    <p class="bold">{{ UserData.name }}</p>
    <p>{{ UserData.name }}</p>
    <p>{{ UserData.idcard}}</p>
    <p class="division">{{ UserData.phone }}</p>
    <p class="bold"></p>
    <p>1. </p>
    <p>2. /18</p>
    <p>3. APP</p>
    <h2 class="sub-title"> </h2>
    <table class="table-box">
      <tr v-for="item in baseTable">
        <td class="half">{{ item.name }}</td>
        <td class="half">{{ item.value }}</td>
      </tr>
    </table>
    <h2 class="sub-title"> </h2>
    <p>1</p>
    <p>2</p>
    <p>3</p>
    <h2 class="sub-title"> </h2>
    <p class="content"></p>
    <h2 class="sub-title"> </h2>
    <p>1</p>
    <p>2</p>
    <p></p>
    <p>3. </p>
    <table class="table-box">
      <tr>
        <td class="third"></td>
        <td class="third"></td>
        <td class="third"></td>
      </tr>
      <tr>
        <td>{{ UserData.hk_date }}</td>
        <td>1</td>
        <td>{{ UserData.yhamount }}</td>
      </tr>
    </table>
    <p>4. </p>
    <p>5</p>
    <p>6. </p>
    <p>7. </p>
    <p>8</p>
    <p>9. </p>
    <h2 class="sub-title"> </h2>
    <p>1. {{ UserData.account }}</p>
    <p class="content">{{ UserData.account_name}}</p>
    <p class="content">{{ UserData.open_bank }}</p>
    <p class="content">{{ UserData.account_number }}</p>
    <p>2. {{ UserData.cardno }}</p>
    <p class="content">{{ UserData.name }}</p>
    <p class="content">{{ UserData.bankcode }}</p>
    <p class="content">{{ UserData.cardno }}</p>
    <h2 class="sub-title"> </h2>
    <p>1</p>
    <p>2</p>
    <p>3. </p>
    <h2 class="sub-title"> </h2>
    <p class="content">5%0.1%/</p>
    <h2 class="sub-title"> </h2>
    <p>1</p>
    <p>2</p>
    <p>3</p>
    <h2 class="sub-title"> </h2>
    <p>1</p>
    <p>2</p>
    <p>3</p>
    <p>4</p>
    <p>5</p>
    <p class="division">6. </p>
    <p>{{ UserData.username }}</p>
    <p>{{ UserData.name }}</p>
    <p class="right-bot">{{ CreateDate }}</p>
  </div>
</template>

<script>

  export default {
    name: 'contractInfo',
    data () {
      return {
        title: '',
        cid: '',
        UserData: {},     //
        baseTable: [{
          name: '',
          key: 'amount',
          value: ''
        },{
          name: '/',
          key: 'jk_day',
          value: ''
        },{
          name: '',
          key: 'create_date',
          value: ''
        },{
          name: '',
          value: '',
        },{
          name: '',
          value: '/'
        }],
        PrintBtn: true,
        CreateDate: ''
      }
    },
    created(){
      this.cid = this.$route.query.id;
      this.InitData({id: this.cid}).then(()=>{
        this.baseTable.forEach(val=>{
          if('key' in val){
            val.value = this.UserData[val.key];
          }
        });
        let dateArr = this.UserData.create_date.split('/');
        this.CreateDate = `${dateArr[0]}${dateArr[1]}${dateArr[2]}`;
      });

    },
    methods: {
      //
      InitData(params = {}){
        const that = this;
        this.loading = true;
        //
        return new Promise((resolve)=>{
          this.$post('Contract/contractShow',params).then((d)=>{
            this.UserData = d.data;
            resolve();
          })
        })
      },
      //
      PrintFunc(){
        this.PrintBtn = false;
        window.print();
      }
    }
  }
</script>


<style lang="less" scoped>
  #contract-info{
    max-width: 750px;
    margin: 0 auto;
    color: #333;
    position: relative;
    p{
      font-size: 14px;
      padding-bottom: 15px;
      line-height: 25px;
      &.bold{
        font-weight: bold;
      }
      &.division{
        padding-bottom: 50px;
      }
      &.content{
        text-indent: 28px;
      }
      &.right-bot{
        text-align: right;
      }
    }
    .printBtn{
      position: fixed;
      bottom: 50px;
      margin-left: -200px;
      color: #FFF;
      font-size: 16px;
      padding: 10px 20px;
      border-radius: 5px;
      background-color: #2DB7F5;
    }
  }
  .all-title{
    text-align: center;
    padding: 20px 0;
  }
  .sub-title{
    font-size: 16px;
    padding: 10px 0 15px;
  }
  .table-box{
    width: 100%;
    border-width: 1px;
    border-color: #666666;
    border-collapse: collapse;
    margin-bottom: 15px;
    td{
      border-width: 1px;
      padding: 8px;
      border-style: solid;
      border-color: #666666;
      background-color: #ffffff;
      font-size: 14px;
      &.half{
        width: 50%;
      }
      &.third{
        width: 33%;
      }
    }
  }
</style>



